.page {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  
  .top-tags {
    bottom: auto;
    top: 15px;
  }
  
  .tags {
    color: var(--cyan);
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 18px;
    font-family: 'La Belle Aurore', cursive;
  }
  
  .bottom-tag-html {
    margin-left: -20px;
  }

  .container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
  }

  .aboutpage,
  .contactpage {
    .text-zone {
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        width: 35%;
        vertical-align: middle;
        display: table-cell;
        max-height: 90%;
        font-family: 'Coo';
    }

    h1 {
        font-size: 53px;
        font-family: 'Coolvetica';
        color: var(--cyan);
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 40px;
        left: 10px;

        &::before {
            content: '<h1>';
            font-family: 'La Belle Aurore', cursive;
            font-size: 18px;
            position: absolute;
            margin-top: -10px;
            left: -10px;
            opacity: 0.6;
            line-height: 18px;
        }

        &::after {
            content: '</h1>';
            font-family: 'La Belle Aurore', cursive;
            font-size: 18px;
            position: absolute;
            left: -30px;
            bottom: -20px;
            margin-left: 20px;
            opacity: 0.6;
        }

    }
    p {
        font-style: 13px;
        color: white;
        font-family: sans-serif;
        font-weight: 300;
        min-width: fit-content;
        animation: pulse 1s;
        font-size: 15px;
        line-height: 25px;

        &:nth-of-type(1){
            animation-delay: 1.1s;
        }

        &:nth-of-type(1)::before {
            content: '<p>';
            font-family: 'La Belle Aurore', cursive;
            position: absolute;
            margin-top: -15px;
            left: -10px;
            opacity: 0.6;
            line-height: 15px;
            font-size: 18px;
        }
        &:nth-of-type(2){
            animation-delay: 1.2s;
        }
        &:nth-of-type(3){
            animation-delay: 1.3s;
        }

        &:nth-of-type(3)::after {
            content: '</p>';
            font-family: 'La Belle Aurore', cursive;
            position: absolute;
            left: -30px;
            bottom: -25px;
            margin-left: 20px;
            opacity: 0.6;
        }
    }

    .text-animate-hover {
        &:hover{
            color: #fff;
        }
    }
  }